/*
 * v-badge max
 */
<script>
import './index.less'
export default {
  install (Vue, options = {}) {
    const NAME = options.name || 'badge'
    const CLASS = 'badge-target-atk'

    if (Vue.directive(NAME) !== void 0) {
      console.warn('已存在v-' + NAME + '指令!')
      return
    }
    let remove = (el) => {
      if (!el) return
      let sup = el.getElementsByClassName(CLASS), len = sup.length, i = 0
      for (; i < len; i++) {
        el.removeChild(sup[i])
      }
    }
    let addSup = (el, binding) => {
      if (!el) return
      remove(el)
      let v = binding.value
      if (!v) return
      let max = el.getAttribute('max') || 999
      v = v > max ? max + '+' : v
      let sup = document.createElement('sup')
      sup.className = CLASS
      sup.innerText = v
      el.appendChild(sup)
    }
    Vue.directive(NAME, {
      bind: (el, binding) => {
        addSup(el, binding)
      },
      unbind: (el) => {
        remove(el)
      },
      componentUpdated: (el, binding) => {
        addSup(el, binding)
      }
    })
  }
}
</script>
