/**
 * this.$message.success.error.warning.info(text, time)
 */
<script>
import Message from './message'

export default {
  install (Vue) {
    if (Vue.$message) {
      console.warn('已存在Vue.$message!')
      return
    }
    let $message
    if (!$message) {
      const MessagePlugin = Vue.extend(Message)
      $message = new MessagePlugin({
        el: document.createElement('div')
      })
      document.body.appendChild($message.$el)
    }
    let add = function (text, time, type) {
      $message.add = {
        time,
        text,
        type
      }
    }
    let message = {
      success (text, time) {
        add(text, time, 'success')
      },
      error (text, time) {
        add(text, time, 'error')
      },
      warning (text, time) {
        add(text, time, 'warning')
      },
      info (text, time) {
        add(text, time, 'info')
      }
    }

    Vue.$message = message

    Vue.mixin({
      created () {
        this.$message = Vue.$message
      }
    })
  }
}
</script>
